@use 'base/variables' as var;
@use 'base/mixins' as m;

.store {
    display: flex;
    flex-direction: column;
    justify-content: center;
    &__heading,
    &__text {
        text-align: center
    }

    &__heading {
        color: var.$secondary;
        margin: 2rem 0 0 0;
    }

    &__text {
        margin-bottom: 3rem;
    }

    &__grid {
        @include m.grid(2, 2rem);
    }
    &__link {
        transition-property: transform;
        transition-duration: 500ms;
        &:hover {
            transform: scale(1.2);
        }
    }
}